import {useState} from "react";
import config from "../../Services/config";

function ModalAutoPark(props) {

    const [url, setUrl] = useState("");
    const [alert, setAlert] = useState("");
    const [name, setName] = useState("");

    let handlerData;
    if ( props.handlerData !== undefined )
        handlerData = props.handlerData.name + ",\n" + props.handlerData.address + ",\n" + props.handlerData.city


    return (
        <div className="modal fade" id="autoParks" tabIndex="-1" aria-labelledby="autoParks"
             aria-hidden="true">
            <div className="modal-dialog modal-fullscreen-lg-down">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Adaugă un park auto.</h5>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close">

                        </button>
                    </div>

                    <div className="modal-body">
                        <div className="form-floating mb-3">
                            <input type="text" className="form-control" id="parkName"
                                   value={name} onChange={(e => {
                                setName(e.target.value);
                            })}/>
                            <label htmlFor="parkName">Dă un nume parcului</label>
                        </div>
                        <div className="form-floating mb-3">
                            <input type="text" className="form-control" id="autoParks"
                                   value={url} onChange={(e => {
                                setUrl(e.target.value);
                                setAlert("");
                            })}/>
                            <label htmlFor="autoParks">Adresa de internet a parcului auto</label>
                        </div>
                        {alert}
                        <div className="form-floating mb-3">
                        <textarea className="form-control" id="exampleFormControlTextarea1" rows="5" readOnly={true}
                                  value={handlerData}>

                        </textarea>

                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Șterge</button>
                        <button type="button" className="btn btn-primary"
                                onClick={(event => {

                                    if ( !url.includes("#ses") ) {
                                        setAlert(validateURL());
                                        return;
                                    }
                                    if ( !url.includes(config.siteValidator) ) {
                                        setAlert(validateURL());
                                        return;
                                    }
                                    return props.handler({
                                        url: url,
                                        name: name
                                    });
                                })}

                        >Cere informația
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

function validateURL() {
    return (
        <div className="alert alert-dark" role="alert">
            Nu este validă adresa.
        </div>
    )
}

const CarsModals = {

    ModalAutoPark: ModalAutoPark

}
export default CarsModals;