
export default function CarsList(props) {


    const {cars,discardVehicle,discardCar} = props


    function TableBodyRow ({el, index}) {
        return (
            <tr>
                <th className="fit">{index + 1}</th>
                <td className="fit"> {el["makeModelDescription"]} </td>
                <td className="fit"> {el["firstRegistration"]} </td>
                <td className="fit"><>{el["mileage"]}</>
                </td>
                <td className="fit"> {el["priceModel"]} </td>
                <td className="fit">
                    <div className="row">
                        <div className="col">

                            {/* eslint-disable-next-line react/jsx-no-target-blank */}
                            <a href={el["url"]} target='_blank'>
                                <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"
                                     className="bi bi-arrow-up-right-square" viewBox="0 0 16 16">
                                    <path fillRule="evenodd"
                                          d="M15 2a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2zM0 2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2zm5.854 8.803a.5.5 0 1 1-.708-.707L9.243 6H6.475a.5.5 0 1 1 0-1h3.975a.5.5 0 0 1 .5.5v3.975a.5.5 0 1 1-1 0V6.707l-4.096 4.096z"/>
                                </svg>
                            </a>
                        </div>
                        <div className="col"
                             onClick={((e) => {
                                 if (discardVehicle!==undefined)
                                 return discardVehicle(el.adID);
                                 if (discardCar!==undefined)
                                 return discardCar(el.adID);
                             })}
                        >

                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor"

                                 className="bi bi-trash" viewBox="0 0 16 16">
                                <path
                                    d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                                <path fillRule="evenodd"
                                      d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                            </svg>
                        </div>
                    </div>
                </td>

            </tr>
        )
    }


    return cars.map((el, index) => {
        return <TableBodyRow key={el["_id"]} el={el} index={index} />;
    });


}
