import React, {useContext, useEffect, useState} from "react";
import HandlersRows from "./handlersRow";
import http from "../../../Services/http";
import config from "../../../Services/config";
import CarsModals from "../modals";
import {CarsContext} from "../CarsContext";
import bootstrap from "bootstrap"

export default  function HandlersList(props) {

    const [handlers, setHandlers] = useState([]);
    const [handler, setHandler] = useState();
    const {cars,setCars} =  useContext(CarsContext)

     useEffect(()=>{
        http.get(config.server + config.api + config.endPoints.handlers, {}).then(results => {
            setHandlers(results.data);
        })

    },[])


    const addHandler = (handler) =>{

        http.post(config.server + config.api + config.endPoints.handlers, {
            url:handler.url,
            name:handler.name
        }).then(results => {
            setHandler(results.data);
            setHandlers(handlers.concat(results.data))
        })

    }
    const selectHandler = async (handler) => {
        const result = await http.get(config.server + config.api + config.endPoints.cars+"/handler/"+handler);
        setCars(result.data);
    }



    return (
        <div className="card">
            <div className="card-header">
                Parcuri Auto
            </div>
            <div className="card-body">

                <div className="table-responsive">

                    <table className="table table-dark table-hover">
                        <thead className="table-dark">
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nume</th>
                            <th scope="col">Țară</th>
                            <th scope="col">Oraș</th>
                            <th scope="col">Tel</th>
                        </tr>
                        </thead>
                        <tbody>
                        <HandlersRows
                            handlers={handlers}
                            selectHandler={selectHandler}
                        />

                        </tbody>
                        <tfoot>

                        </tfoot>

                    </table>
                </div>

            </div>
            <div className="card-footer text-muted">
                <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#autoParks" >Adaugă parc auto</button>
            </div>
            <CarsModals.ModalAutoPark
            handler={addHandler}
            handlerData={handler}
            />

        </div>
    )

}