import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BrowserRouter,Switch, Route} from "react-router-dom";
import IndexAdmin from "./Admin";
import Navigation from "./Navigation/Navigation";
import IndexCars from "./CarsX";
import FrontEnd from "./FrontEnd";
import FrontEndLogin from "./FrontEnd/Landing/login";
import config from "./Services/config";
import { io } from "socket.io-client";

const socket= io(config.server)

window.socket= socket;

ReactDOM.render(
    <BrowserRouter>
        <Navigation/>
        <Switch>
            <Route exact path="/" component={FrontEnd} />
            <Route exact path="/login" component={FrontEndLogin} />
            <Route exact path="/admin/login" component={App} />
            <Route exact path="/dashboard" component={IndexAdmin} />
            <Route exact path="/prices" component={App} />
            <Route exact path="/cars" component={IndexCars} />
        </Switch>
    </BrowserRouter>,
  document.getElementById('root')
);


