import CarsStart from "./Landing/headerCarsStart";
import FrontEndLogin from "./Landing/login";
import FadeIn from "react-fade-in";
import FrontAdvantages from "./Landing/advantajes";
import {useEffect} from "react";


export default function FrontEnd() {

    const {socket}=window;
    useEffect(()=>{

        return () => socket.disconnect();

    });

    return (

        <main>
            <div className="App">
                <div className="App-header">
                    <FadeIn delay={100}>
                        <CarsStart/>
                    </FadeIn>
                    <FadeIn delay={1000} visible={true}>

                        <FrontEndLogin/>
                    </FadeIn>
                    <FadeIn delay={1000} visible={true}>

                        <FrontAdvantages/>
                    </FadeIn>

                </div>
            </div>

        </main>

    )
}