import {useContext} from "react";
import CarsList from "./CarsList";
import {CarsContext} from "./CarsContext";
import http from "../../Services/http";
import config from "../../Services/config";

export default function Cars(props) {


    const {cars,setCars} =  useContext(CarsContext)

    const discardCar = async (id) => {

        const index = cars.findIndex(vehicle => vehicle.adID === id);
        const newVehicles = JSON.parse(JSON.stringify(cars));
        newVehicles.splice(index, 1);
        setCars(newVehicles);

        await http.delete(config.server + config.api + config.endPoints.cars + "/" + id, {})

    }

    return (
        <div className="card mt-2">
            <div className="card-header">
                Mașini
            </div>
            <div className="card-body">
                <div className="table-responsive">
                    <div className="">
                        <table className="table table-sm table-dark table-striped">
                            <thead className="table-dark">
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Marca, Modelul</th>
                                <th scope="col">Anul fabricațíei</th>
                                <th scope="col">KM</th>
                                <th scope="col">Preț</th>
                                <th scope="col">Operații</th>
                            </tr>
                            </thead>
                            <tbody>
                            <CarsList
                                cars={cars}
                                discardCar = {discardCar}
                            />

                            </tbody>
                        </table>

                    </div>

                </div>
            </div>
        </div>
    )

}