import {Link} from "react-router-dom";
import {useState} from "react";

export default function FrontEndLogin() {

    const LoginForm = () => {
        return(

        <section className="d-flex flex-column foreground  bg-dark text-center ">
                <h1 className="display-4 fw-bold text-white  mt-5 mb-5">Înscriete</h1>
            <div className="container mb-3">
                <div className="row justify-content-center justify-content-lg-between mx-lg-5 mx-sm-0">
                    <div className="col-md-10 col-lg-6 col-xl-5 mb-1 text-lg-start ">
                        <h1 className="lh-sm text-white">Cel mai bun tracker de mașini.</h1>
                        <small className="d-block text-muted">Cele mai bune prețuri.</small>
                    </div>
                    <div className="col-md-10 col-lg-6 col-xl-5">
                        <div className="card bg-opaque-black">
                            <div className="card-body bg-white align-content-center">
                                <form action="#" className="row g-2">
                                    <div className="col-lg-12">
                                        <div className="form-floating">
                                            <input type="text" className="form-control" id="floatingInput-1"
                                                   placeholder="Numele tău"/>
                                                <label htmlFor="floatingInput-1">Numele tău</label>
                                        </div>
                                    </div>

                                    <div className="col-lg-12">
                                        <div className="form-floating">
                                            <input type="email" className="form-control" id="floatingInput-3"
                                                   placeholder="name@example.com"/>
                                                <label htmlFor="floatingInput-3">Adresa email</label>
                                        </div>
                                    </div>

                                    <div className="text-center align-content-center">
                                        <Link to="" className="btn btn-dark btn-lg px-4 me-sm-3 fw-bold">Trimite</Link>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        )
    }
    const [form, setForm] = useState(<LoginForm/>)

    return (

        <></>





    )
}