
export default function HandlersRows(props) {


    const handlers = props.handlers;
    const handlersRow = (el, index) =>{
        return (

            <tr key={el["_id"]} onClick={(e => props.selectHandler(el._id))}>
                <th >{index + 1}</th>
                <td className="fit"> {el["name"]} </td>
                <td className="fit"> {el["country"]} </td>
                <td className="fit"> {el["city"]} </td>
                <td className="fit"> {el["phone"]} </td>
            </tr>
        )

    }


    return handlers.map((el, index) => {
        return handlersRow(el, index);
    });
}