import {Link} from "react-router-dom";
import StartCards from "./startCards";
import http from "../../Services/http";
import config from "../../Services/config";
import {useEffect, useState} from "react";

export default function  CarsStart(){

    const [data,setData] = useState([]);

    useEffect(() =>{
        http.get(config.server + config.api + config.endPoints.vehicles+"/start",{}).then(result =>{
            setData(result.data);
        })
        console.log(data);
    },[])

    return(
        <div className="bg-darkgray text-secondary px-2 py-2 text-center">
            <div className="py-2">
                <div className="container">

                <h1 className="display-4 fw-bold text-white">Căutare de mașini ieftine</h1>
                        <div className="d-grid gap-5 d-flex justify-content-center">
                            <Link
                                type="button"
                                className="btn btn-outline-info btn-lg px-4 me-sm-3 fw-bold"
                                to="/vehicles"
                            >
                                Vezi mașini

                            </Link>
                        </div>
                            <div className="row justify-content-center" >
                                <StartCards
                                    data={data}
                                />


                            </div>
                </div>



            </div>
        </div>
    )

}