import CarsList from "../../Admin/src/CarsList";
import {useEffect, useState} from "react";
import http from "../../Services/http";
import config from "../../Services/config";

export default function CarsCard(props) {

    const [cars, setCars] = useState([]);
    const [makes, setMakes] = useState([]);
    const [page, setPage] = useState(1);
    const {newVehicle} = props;


    async function discardVehicle(id) {
        const index = cars.findIndex(vehicle => vehicle.adID === id);
        const newVehicles = JSON.parse(JSON.stringify(cars));
        newVehicles.splice(index, 1);
        setCars(newVehicles);
        await http.delete(config.server + config.api + config.endPoints.vehicles + "/" + id, {})
    }


    const itemConstruct = async (vehicle) => {

        if (vehicle.attributes.length > 0) {
            //finding the first registration date.
            vehicle.attributes.map(attribute => {
                if (attribute.tag === "firstRegistration") {
                    return vehicle["firstRegistration"] = attribute.value;
                }
                if (attribute.tag === "mileage") {
                    return vehicle["mileage"] = attribute.value.replace("&nbsp;", " ");
                }
            })
            vehicle["adID"] = vehicle.id;
            vehicle["makeModelDescription"] = vehicle.title;
            vehicle["priceModel"] = vehicle["price"]["grs"]["localized"];
            delete vehicle.attributes;
            delete vehicle.id;
            delete vehicle.title;
            delete vehicle["price"];
        }
        return vehicle;

    }


    const fetchData = async () => {
        const fetchItems = await http.get(config.server + config.api + config.endPoints.vehicles + "/page/" + page, {});
        const vehicles = await fetchItems.data;
        if (vehicles.length > 0) {
            // array returned from endPoint
            await vehicles.map(async vehicle => {
                //checking the length of the attributes array
                await itemConstruct(vehicle)
                return vehicle;
            })
            await setMakes([...new Set(
                vehicles.map(car => car["make"]["localized"])
            )]);
            await setCars(vehicles);
        }
    }


    useEffect(() => {
        (async () => {
            await fetchData()
        })();
    }, [])

    useEffect(()=>{
        (async ()=>{
            if (newVehicle!==null){
                const newItem = await itemConstruct(JSON.parse(newVehicle));
                setCars(prevState => {
                    return[newItem,...prevState];
                });
            }

        })()

    },[newVehicle])


    function BrandSelectorFilter({makes}) {
        if (makes === undefined)
            makes = [];
        return makes.map(make => {
            return (
                <option key={make}>{make}</option>
            )
        })
    }

    const brandFilterSelect = (choice)=>{



    }

    return (
        <div className="card text-start mt-2">
            <div className="card-header">
                Mașini
            </div>
            <div className="card-body">
                <div className="row my-2 mb-2">
                    <div className="col-lg-3">

                    </div>

                </div>

                <div className="table-responsive">
                    <div className="">
                        <table className="table table-sm table-dark table-striped table-hover">
                            <thead className="table-dark">
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">
                                    <select className="form-select" aria-label=".form-select-sm example" onChange={(event) => brandFilterSelect(event.target.value) }>
                                        <option defaultValue>Selectează marca</option>
                                        <BrandSelectorFilter makes={makes}/>
                                    </select>
                                </th>
                                <th scope="col">Anul fabricațíei</th>
                                <th scope="col">KM</th>
                                <th scope="col">Preț</th>
                                <th scope="col">Operații</th>
                            </tr>
                            </thead>
                            <tbody>
                            <CarsList
                                key={"none"}
                                cars={cars}
                                discardVehicle={discardVehicle}
                            />
                            </tbody>
                        </table>

                    </div>

                </div>


            </div>
            <div className="card-footer text-muted">
            </div>
        </div>
    )

}
