import CarsCard from "./src/CarsCard";
import {useEffect, useState} from "react";
import config from "../Services/config";


export default function IndexCars() {

    const {socket}=window;
    const [newVehicle,setNewVehicle] = useState(null);

    useEffect(()=>{
        socket.connect(config.server);
    })
    useEffect(()=>{
        socket.on("newcar",(vehicle)=>{
            setNewVehicle(vehicle);
        })
    })

    return (
        <div className="App">
            <div className="App-header">
                    <CarsCard newVehicle={newVehicle} key={"key"}/>
            </div>
        </div>
    )

}
