import AdminDashboard from "./src/AdminDashboard";
import HandlersList from "./src/Handlers/HandlersList";
import Cars from "./src/Cars";
import {CarsContext} from "./src/CarsContext";
import http from "../Services/http";
import config from "../Services/config";
import {useEffect, useMemo, useState} from "react";


export default function IndexAdmin(props) {

    const [cars, setCars] = useState([]);
    const [page, setPage] = useState(1);
    const carsProvider = useMemo(()=> ({cars,setCars}),[cars,setCars])


    useEffect(() => {


        http.get(config.server + config.api + config.endPoints.cars + "/" + page, {}).then(result => {
            let results = result.data
            results = results.map(el => {
                let url= el.url[0]["url"];
                delete el.url;
                el["url"] =url;
                return el;
            })
            setCars(results);
        })


    }, [])



    return (

        <>

            <div className="App">

                <div className="App-header">
                    <div className="container">

                        <AdminDashboard
                        />
                        <CarsContext.Provider value={carsProvider}>
                        <HandlersList
                        />
                        <Cars
                        />
                        </CarsContext.Provider>
                    </div>
                </div>
            </div>
        </>

    )


}