export default function StartCards(props){


    const data = props.data;
    const cardStyle = {
        width:"20rem"
    }
    const imgStyle = {
        height:250
    }

    return  data.map(el => {

        return(


            <div className="col-lg-4 d-flex align-self-stretch justify-content-center" key={el._id}>

                <div className="card other-card mt-3  justify-content-between align-text-bottom" style={cardStyle}>
                    <div>

                    <img src={"https://"+el.images[0]["uri"]+"?rule=mo-1024.jpg"} className="img-fluid" alt="" width="auto" style={imgStyle}/>
                    </div>
                    <div className="card-body d-flex flex-column">
                        <div className="mt-auto">

                            <h5 className="card-title">{el['makeKey']+" "+el['model']['localized']}</h5>
                            <p>Preț : <em>{el["price"]["grs"]["amount"]} €.</em></p>
                            <p><em>{el["year"]} | {el["fuel"]} | {el["mileage"]} </em></p>
                            <p className="card-text"><small className="text-muted"> </small></p>
                        </div>
                    </div>
                </div>
            </div>

        )
    })

}